var expressionSet = {
    NEQ: '\\\\neq',//
    MUL: '\\\\times', //
    DIV: "\\\\div",   //
    EQU: '\\\\eq',    //
    PLUS: '\\\\plus',  //
    MINUS: '\\\\minus',//
    LBRACE: '\\\\lbrace',//
    RBRACE: '\\\\rbrace',//
    LBRACKET: '\\\\lbracket',//
    RBRACKET: '\\\\rbracket',//
    FRACTION: "\\\\frac{.*?}{.*?}",//
    UNIT: "\\\\unit{.*?}{.*?}{.*?}",//
    UNKN: "\\\\var{.+?}",//
    NEWLINE: "\\\\n(?!(eq))",//
    SPACE: '    ',//
    WORD_ANSWER: '\\\\wordAnswer{(.+)}',//
    SQUARE: '\\^([a-z]|\\d)*', //,
    GREATER: '\\\\gt',//
    LESS: '\\\\lt',//
    LE: '\\\\le',//
    GE: '\\\\ge',//
    APPROX: '\\\\approx',//
    PI: '\\\\pi',//
    REMAIN: '\\\\remain',//
    COMMENT: '\\\\comment{(.*?)}',//
    PERCENT:'\\\\\\%'//
};
var unSupportExpressionSet = {
    EQU: {expr: '\\eq', directive: 'equal'},//
    PLUS: {expr: '\\plus', directive: 'plus'},//
    MINUS: {expr: '\\minus', directive: 'minus'},//
    LBRACKET: {expr: '\\\\lbracket', directive: 'lbracket'},//
    RBRACKET: {expr: '\\\\rbracket', directive: 'rbracket'},//
    FRACTION: {expr: "\\\\frac{(.*?)}{(.*?)}", directive: 'frac'},//
    UNIT: {expr: '\\\\unit{.*?}{.*?}{.*?}', directive: 'unit'},//
    UNKN: {expr: '\\\\var{.+?}', directive: 'unkn'},//
    NEWLINE: {expr: '\\\\n(?!(eq))', directive: 'newline'}, //
    SPACE: {expr: '    ', directive: 'space'},//
    REMAIN: {expr: '\\\\remain', directive: 'remain'},//
    WORD_ANSWER: {expr: '\\\\wordAnswer{(.+)}', directive: 'word-answer'},//
    COMMENT: {expr: '\\\\comment{(.*?)}', directive: 'comment'}//

};
var mergedExpressionSet = {};
Object.assign(mergedExpressionSet, expressionSet);
Object.assign(mergedExpressionSet, unSupportExpressionSet);

export {expressionSet,unSupportExpressionSet, mergedExpressionSet}



